@import url(https://fonts.googleapis.com/css?family=Maven+Pro);
.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 112vh;
  margin: -8px;
  align-items: center;
  font-family: "Inter";
  background-image: url(/static/media/bg1.9b853b5c.jpg);
  background-size: cover;
}

.glass {
  position: absolute;
  top: 1em;
  padding: 3em;
  width: 35em;
  height: 100vh;
  z-index: 1;
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  box-shadow: 0 0 10px rgb(190 147 58);
  transition: 0.5s;
  transition-delay: 1s;
  color: blanchedalmond;
  text-shadow: 1px 1px 8px black;
  border-radius: 15px;
}
.fade{
  position:fixed;
  background: rgba(0,0,0,.3);
  height:100%;
  width:100%;
  left:0;
  top:0;
  z-index:0;
}
.login__form {
  display: flex;
  flex-direction: column;
}

.login__form > h1 {
  font-size: 35px;
  margin-bottom: 15px;
  padding-bottom: 10px;
  letter-spacing: -1px;
}

.login__form > input {
  min-width: 380px;
  padding: 25px 0;
  padding-left: 15px;
  margin-bottom: 25px;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.24);
  font-family: "Inter";
  border-radius: 15px;
  font-size: 20px;
}

.submit__btn {
  padding: 17px 30px;
  margin-top: 10px;
  background: #be933a;
  cursor: pointer;
  border: none;
  color: white;
  font-size: 14px;
  font-family: "Inter";
  border-radius: 3px;
}

.frm-container {
  display: flex;
  padding: 10px 10px;
  justify-content: space-between;
}

.frm-opt {
  margin-left: 2em;
  font-size: 20px;
}

h3 {
  display: flex;
}
.home{
  height: auto;
  width: 100%;
  min-width: 800px;
  /* background: linear-gradient(to bottom right, #9c27b0, #00BCD4); */
  background-image: url(/static/media/bg.791cca6a.jpg);
  background-size: cover;
  /* display: flex; */
  padding-top: 7em;
  justify-content: center;
  /* align-items: center; */
  color: #fff;
  text-align: center;
}
.fade{
  position:fixed;
  background: rgba(0,0,0,.3);
  height:100%;
  width:100%;
  left:0;
  top:0;
  z-index:0;
}
h1{
  margin-bottom:2rem;
  color:#fff;
  text-transform:uppercase;
  position:relative;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #000, 0 0 30px #000, 0 0 40px #000, 0 0 55px #000, 0 0 75px #000;
  letter-spacing:4px;
}

.countbox{
  padding: 5rem 7rem;
  background:rgb(255 255 255 / 42%);
  border-radius:20px;
  box-shadow: 3px 3px 10px rgba(0,0,0,.1);
  display:inline-block;
  position:relative;
  margin-bottom: 2em;
}
.countbox > p {
  background: rgba(0,0,0,.4);
  color: #fff;
  padding: 2em 4em;
  border-radius: 20px 0;
  margin: 0 1.7rem;
  width: -1rem;
  font-size: 2.5rem;
  text-align: center;
  display: inline-flex;
  position: relative;
}
.countbox > span{
  position:absolute;
  top:calc(100% + .5rem);
  left:.2rem;
  font-size:.8rem;
  text-transform:uppercase;
}

.add {
  padding: 2em;
}

/* logout */
.logout {
  padding: 2em;
  margin-top: -7em;
}

.logout__button {
  min-width: 300px;
  padding: 17px 30px;
  margin-top: 20px;
  background: black;
  border: none;
  color: white;
  font-size: 14px;
  font-family: "Inter";
  border-radius: 3px;
}

.user__name {
  color: #8d0ada;
}

table {
  display: block;
  padding: 2em;
  font-size: 2em;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  margin:0;
  padding:0;
  transition:.3s all ease;
  box-sizing:border-box;
  font-family: 'Maven Pro', sans-serif;
}
